import React, {Component} from 'react';
import {Carousel, CarouselCaption, CarouselInner, CarouselItem, View, Mask} from 'mdbreact';
import Layer1 from '../images/slider/Layer 12.png';
import Layer2 from '../images/citygif.gif';
import Layer3 from '../images/Modern city of Singapore wallpaper.jpg';
import Layer4 from '../images/city2.jpg';


class CarouselPage extends Component {
    render() {
        return (
            <Carousel activeItem={1} length={4} showControls={true} showIndicators={true} className="z-depth-1" id='carusel'>
                <CarouselInner>
                    <CarouselItem itemId="1">
                        <View>
                            <img className="d-block w-100" src={Layer1} alt="First slide"/>
                            <Mask overlay="black-light"/>
                        </View>
                        <CarouselCaption>
                        </CarouselCaption>
                    </CarouselItem>
                    <CarouselItem itemId="2">
                        <View>
                            <img className="d-block w-100" src={Layer2} alt="Second slide"/>
                            <Mask overlay="black-strong"/>
                        </View>
                        <CarouselCaption>
                        </CarouselCaption>
                    </CarouselItem>
                    <CarouselItem itemId="3">
                        <View>
                            <img className="d-block w-100" src={Layer3} alt="Third slide"/>
                            <Mask overlay="black-slight"/>
                        </View>
                        <CarouselCaption>
                        </CarouselCaption>
                    </CarouselItem>
                    <CarouselItem itemId="4">
                        <View>
                            <img className="d-block w-100" src={Layer4} alt="Mattonit's item"/>
                            <Mask overlay="black-light"/>
                        </View>
                        <CarouselCaption>
                        </CarouselCaption>
                    </CarouselItem>
                </CarouselInner>
            </Carousel>
        );
    }
}

export default CarouselPage;