import React, {Component} from 'react';
import Search from '../images/Layer2.png';
import {Link } from 'react-router-dom';

class Nav extends Component {
    constructor(props) {
        super(props);
    }

    handle(){
        document.getElementById('ul').classList.toggle('open');
        document.getElementById('carusel').classList.toggle('openUl');
        
    }

    render() {
        return (
            <nav>
                <Link to='/react/' className="logo">React Blog</Link>
                <img src={Search}/>
                <a className='itemGray'>About</a>
                <a className='itemGray'>Cooperation</a>
                <a className='itemGray'>Contact</a>

                <ul id='ul'>
                    <li><Link to='/article/1'>Nature</Link></li>
                    <li><Link to='/article/2'>People</Link></li>
                    <li><Link to='/article/5'>Tips</Link></li>
                    <li><Link to='/article/4'>Animals</Link></li>
                    <li><Link to='/article/6'>Fashion</Link></li>
                    <li><Link to='/article/3'>Tech</Link></li>

                </ul>
                <button onClick={this.handle} id='menu'>&#9776;</button>
            </nav>
        );
    }
}


export default Nav;