import React, {Component} from 'react';
import CardExample from './CardExample';
import ImgHeader from '../images/Huge box/Layer 11.png';
import items from '../items';
import { Link } from 'react-router-dom';




class Sec2 extends Component {
    constructor(){
        super();
    }

    render(){
        return(
            <div className='sec2'>
                <CardExample key={'Hedaer'} Source={ImgHeader} Title={'Trips'} Text={'Lorem ipsum dolor'} Ruby={'11 November 2016'} Link={'Link'} Href={'http://www.fb.com'}/>
                <div className='sec2Items'>
                    {items.all().map(function(a){
                        return <Link key={a.number} to={`/article/${a.Number}`}><CardExample  Source={a.Source} Title={a.Title} Text={a.Text} Ruby={a.Ruby} Link={a.Link} Href={a.Href}/></Link>;
                    })}
                </div>
            </div>
        );
    }
}


export default Sec2;


