import React, {Component} from 'react';
import CardExample from './CardExample';
import Example from '../api';


class Sec1 extends Component{
    constructor(){
        super();
    }

    render(){
        return(
            <div>
                {Example.all().map(function(p){
                    return <CardExample key={p.number} Source={p.Source} Title={p.Title} Text={p.Text} Ruby={p.Ruby} Link={p.Link} Href={p.Href}/>;
                })}
            </div>
        );
    }
}


export default Sec1;