import React, {Component} from 'react';
import Nav from './Nav';
import CarouselPage from './CarouselPage';
import Sec1 from './Sec1';
import Sec2 from './Sec2';
import Footer from './Footer';
import {Switch, Route} from 'react-router-dom';
import Home from './Home';
import Home2 from './Home2';
import Home3 from './Home3';
import Home4 from './Home4';
import Home5 from './Home5';
import Home6 from './Home6';

class App extends Component {
    render() {
        return (
            <div>
                <Nav/>
                <CarouselPage/>
                <Sec1/>
                <Switch>
                    <Route exact path='/react/' component={Sec2}/>
                    <Route path='/article/1' component={Home}/>
                    <Route path='/article/2' component={Home2}/>
                    <Route path='/article/3' component={Home3}/>
                    <Route path='/article/4' component={Home4}/>
                    <Route path='/article/5' component={Home5}/>
                    <Route path='/article/6' component={Home6}/>
                </Switch>
                <div className='sec3'>
                    <Sec1/>
                </div>
                <Footer/>

            </div>
        );
    }
}

export default App;

