import Img1 from './images/Big box/Layer3.png';
import Img2 from './images/Big box copy/Layer 4.png';

const Example = {
    cards: [
        {'Number': '1','Source': Img1 ,'Title': 'Trips' , 'Text': 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Laudantium, quisquam.Lorem ipsum dolor sit amet, consectetur', 'Ruby': '11 September 2016' , 'Link': 'John Doe' ,'Href' : 'http://www.fb.com'},
        {'Number': '2','Source': Img2 ,'Title': 'Tech' , 'Text': 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Laudantium, quisquam.Lorem ipsum dolor sit amet, consectetur', 'Ruby': '24 November 2017' , 'Link': 'John Doe','Href' : 'http://www.fb.com'},

    ],
    all: function() { return this.cards; },
    get: function(id) {
        const isCard = p => p.number === id;
        return this.cards.find(isCard);
    }
};

