import React, {Component} from 'react';
import CardExample from './CardExample';
import {Link} from 'react-router-dom';


class Home4 extends Component {
    constructor() {
        super();
    }

    render() {
        return (
            <div className='article trips'>
                <CardExample
                    key={'Hedaer'}
                    Source='http://weflex.am/Blog/Layer7.png'
                    Title={'Trips'}
                    Text={'Fusce hendrerit nisi eu placerat blandit.'}
                    Ruby={'11 November 2016'} Link={'Link'} Href={'http://www.fb.com'}/>
                <p className='bottomText'>Aenean faucibus dui vel aliquam egestas. Fusce ut nibh elit. Fusce vel leo id arcu convallis ultricies. Morbi convallis vehicula tortor, quis vestibulum felis condimentum convallis. Curabitur id ipsum ac.
                </p>
                <Link className="buttonBack" to='/react/'>Back</Link>
            </div>
        );
    }
}


export default Home4;


